import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import uniRequest from 'uni-request';
export default {
	components: {
		uniLoadMore	
	},
	data() {
		return {
			cateMaskState: 0, //分类面板展开状态
			headerPosition:"fixed",
			headerTop:"0px",
			loadingType: 'more', //加载更多状态
			filterIndex: 0, 
			cateId: 0, //已选三级分类id
			priceOrder: 0, //1 价格从低到高 2价格从高到低
			cateList: [],
			goodsList: [],
			page:1,
			page_size:10,
			ordering:'create_time'
		};
	},
	
	onLoad(options){



		console.log(options)
		this.cateId = options.tid;
		this.loadCateList(options.fid,options.sid);
		this.loadData();
	},
	onPageScroll(e){
		//兼容iOS端下拉时顶部漂移
		if(e.scrollTop>=0){
			this.headerPosition = "fixed";
		}else{
			this.headerPosition = "absolute";
		}
	},
	//下拉刷新
	onPullDownRefresh(){
		this.loadData('refresh');
	},
	//加载更多
	onReachBottom(){
		this.page_size = this.page_size + 10
		this.loadData();
	},
	methods: {
		//加载分类
		async loadCateList(fid, sid){
			/* let list = await this.$api.json('cateList');
			let cateList = list.filter(item=>item.pid == fid);
			
			cateList.forEach(item=>{
				let tempList = list.filter(val=>val.pid == item.id);
				item.child = tempList;
			})
			this.cateList = cateList;
			console.log(this.cateList) */
		},
		//加载商品 ，带下拉刷新和上滑加载
		async loadData(type='add', loading) {
			//没有更多直接返回
			if(type === 'add'){
				if(this.loadingType === 'nomore'){
					return;
				}
				this.loadingType = 'loading';
			}else{
				this.loadingType = 'more'
			}
			
			const response = await uniRequest.get('/categories/'+this.cateId+'/skus/?page='+this.page+'&page_size='+this.page_size+'&ordering='+this.ordering);
			console.log(response)
			if(response.status === 200){
				this.goodsList = response.data.results;
				console.log(this.goodsList)
				
				//判断是否还有下一页，有是more  没有是nomore(测试数据判断大于20就没有了)
				this.loadingType  = this.goodsList.length > 100 ? 'nomore' : 'more';
				if(type === 'refresh'){
					if(loading == 1){
						uni.hideLoading()
					}else{
						uni.stopPullDownRefresh();
					}
				}
			}
			
			/* let goodsList = await this.$api.json('goodsList');
			if(type === 'refresh'){
				this.goodsList = [];
			}
			//筛选，测试数据直接前端筛选了
			if(this.filterIndex === 1){
				goodsList.sort((a,b)=>b.sales - a.sales)
			}
			if(this.filterIndex === 2){
				goodsList.sort((a,b)=>{
					if(this.priceOrder == 1){
						return a.price - b.price;
					}
					return b.price - a.price;
				})
			} */
			
			
		},
		//筛选点击
		tabClick(index){
			if(this.filterIndex === index && index !== 2){
				return;
			}
			this.filterIndex = index;
			if(index === 2){
				this.priceOrder = this.priceOrder === 1 ? 2: 1;
			}else{
				this.priceOrder = 0;
			}
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			this.loadData('refresh', 1);
			uni.showLoading({
				title: '正在加载'
			})
		},
		//显示分类面板
		toggleCateMask(type){
			let timer = type === 'show' ? 10 : 300;
			let	state = type === 'show' ? 1 : 0;
			this.cateMaskState = 2;
			setTimeout(()=>{
				this.cateMaskState = state;
			}, timer)
		},
		//分类点击
		changeCate(item){
			console.log(item)
			this.cateId = item.id;
			this.toggleCateMask();
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			this.loadData('refresh', 1);
			uni.showLoading({
				title: '正在加载'
			})
		},
		//详情
		navToDetailPage(item){
			console.log(item)
			let id = item.id
			//测试数据没有写id，用title代替
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		},
		stopPrevent(){}
	},
}
